* {
  margin: 0;
}

.app {
  display: flex;
  justify-content: center;
}

.main {
  margin-top: 50px;
}

.CV-form {
  display: grid;
  grid-template-rows: minmax(100px, auto) minmax(250px, auto) minmax(250px, auto) 50px;
}

button {
  cursor: pointer;
}

button[type="submit"] {
  justify-self: center;
  width: 200px;
}

.generalInformation,
.educationExperience,
.practicalExperience {
  border: 1px black solid;
  margin-bottom: 20px;
  padding: 15px;
  width: 75vw;
}

.generalInformation-InputContainer,
.practicalExperience-InputContainer,
.educationExperience-InputContainer {
  display: flex;
  gap: 9px;
  flex-wrap: wrap;
  margin-bottom: 5px;
}

.Title {
  margin-bottom: 10px;
}

.mainTitle {
  text-align: center;
  font-size: 30px;
  margin-bottom: 20px;
}
